.tiles {
    display: flex;
    flex-flow: row wrap;
    margin: auto;
}

/* Website specific */

.big-project {
    width: calc((100% / 2) - 20px);
    margin: 0.5em;
    & img {
        width: 100%;
    }
}

/* Personal Project Specific */

.project {
    width: calc((100% / 4) - 0.5em);
    margin: 0.5em 0.1em;
    & img {
        width: 100%;
    }
}

.project-container {
    position: relative;
    text-align: center;
    color: white;
    width: 100%;
}

.container-caption {
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content:center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
        &:hover {
            background-color: brown;
            width: 100%;
            height: 100%;
        }
}

figcaption {
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 0.3s;
        & h1{
            margin: 0;
        }
        & p {
            margin: 1em 0;
        }
        &:hover {
            opacity: 1;
        }
}

figure {
    margin: 0;
}